<template>
  <div class="myHeaderBox">
    <div class="mycell myMenu">
      <i class="fa fa-align-justify"></i>
    </div>
    <div class="mycell myUser">
      <i class="el-icon-search"></i>
      <i class="fa fa-fonticons"
         @click="routerFn"></i>
      <i class="fa fa-user"></i>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    routerFn() {
      window.location.href = "./#/fontHelp";
    }
  }
};
</script>
<style socped>
.myHeaderBox {
  width: 100%;
  height: 60px;
  line-height: 60px;
  padding: 0 10px;
  font-size: 36px;
  color: #fff;
}
.myHeaderBox .mycell {
  display: inline-block;
  float: left;
  width: calc(50% - 10px);
  border: 0;
}
.myHeaderBox .myMenu {
  text-align: left;
}
.myHeaderBox .myUser {
  text-align: right;
}
</style>



